import { back, sort } from "core";
import React from "react";

export type PaginatorProps = {
  currentPageIdx: number, // zero indexed
  numKnownPages: number,
  mightHaveMorePages: boolean,
  onSetPage: (pageIdx: number) => void,
}

export class Pagniator extends React.Component<PaginatorProps, {}> {
  /**
   * Make class parameters for a paginator choice
   * @param enabled Should this choice be clickable
   * @param active Is this choice currently active
   */
  _makeClassString (enabled: boolean = false, active: boolean = false): string {
    let className = 'py-1 px-2 rounded-md drop-shadow-md  '
    if (active)
      return className += 'bg-lime-500'
    if (enabled)
      return className + 'bg-lime-300 hover:bg-lime-400 cursor-pointer'
    return className + 'bg-lime-50 text-slate-500'
  }

  /**
   * Make a paginator choice to navigate to the specified absolute page index
   * @param pageIdx page index to navigate to  (zero indexed)
   */
  _makeAbsoluteChoice (pageIdx: number): JSX.Element {
    const onClick = () => this.props.onSetPage(pageIdx)
    const classString = this._makeClassString(true, this.props.currentPageIdx === pageIdx)
    return <div className={classString} onClick={onClick} key={pageIdx}>{pageIdx + 1}</div>
  }

  _makeRelativeChoice (isIncrease: boolean, key: string): JSX.Element {
    const shift = isIncrease ? 1 : -1
    const enabled = isIncrease ? this.props.currentPageIdx < this.props.numKnownPages  - 1: this.props.currentPageIdx > 0
    const symbol = isIncrease ? '>' : '<'
    const onClick = enabled ? () => this.props.onSetPage(this.props.currentPageIdx + shift) : () => null
    const classString = this._makeClassString(enabled, false)
    return <div className={classString} onClick={onClick} key={key}>{symbol}</div>
  }

  _makeEllipses (key: string): JSX.Element {
    return <div className='py-1 px-2' key={key}>...</div>
  }

  render(): JSX.Element {
    // collect nodes to show
    const nodes: JSX.Element[] = []
    
    // always start with <
    nodes.push(this._makeRelativeChoice(false, 'decrease'))

    // determine which indices should be shown as absolute choices
    //  always show page 0 and pages with +/-1 of the current
    let shownPageIndices = [0, this.props.currentPageIdx - 1, this.props.currentPageIdx, this.props.currentPageIdx + 1]
    shownPageIndices = sort(Array.from(new Set(shownPageIndices)).filter((x) => x >= 0 && x < this.props.numKnownPages))
    
    // collect absolute chocies + ellipses
    for (let i = 0; i < shownPageIndices.length; i++) {
      // either fill gaps or show trailing ellipses
      const gap = i > 0 ? shownPageIndices[i] - shownPageIndices[i - 1] : 0
      if (gap === 2) 
        nodes.push(this._makeAbsoluteChoice(shownPageIndices[i] - 1))
      else if (gap > 2)
        nodes.push(this._makeEllipses(`${shownPageIndices[i] - 1}`))
      
      // add the choice
      nodes.push(this._makeAbsoluteChoice(shownPageIndices[i]))
    }

    // show ... if there might be more nodes
    if (this.props.mightHaveMorePages || back(shownPageIndices) < this.props.numKnownPages - 1) 
    nodes.push(this._makeEllipses('end'))

    // always show >
    nodes.push(this._makeRelativeChoice(true, 'increase'))

    // render nodes
    return <div className="flex gap-2 items-center text-sm">{nodes}</div>
  }
}
