import React from 'react';

import { Page } from 'cfg/pages'


export class SideBar extends React.Component<{page: Page, setPage: (page: Page) => void}, {}> {
  _makeLabel (page: Page): string {
    const kind = page.kind
    switch (kind) {
      case 'history':
        return 'Calls'
      case 'home':
        return 'Summary'
      default:
        const _exhaustive: never = kind
        return _exhaustive
    }
  }
  
  _makeOption(page: Page): JSX.Element {
    // make page class string
    let className: string = 'p-2 '
    if (this.props.page.kind === page.kind) 
      className += 'cursor-default font-bold bg-slate-600'
    else
      className += 'cursor-pointer hover:bg-slate-700'

    // make click event handler
    const onClick = () => this.props.setPage(page)

    // return element
    const label = this._makeLabel(page)
    return <div data-page-name="home" className={className} onClick={onClick}>{label}</div>
  }
  
  render(): JSX.Element {
    return (
      <div className='flex flex-col w-32 h-screen pt-4 gap-4 bg-slate-800 text-white font-medium text-xl'>
        {this._makeOption({kind: 'home'})}
        {this._makeOption({kind: 'history'})}
      </div>
    )
  }
}